@import './materialize';
.inline-block {
  display: inline-block; }
nav#nav {
  background: #fff;
  .brand-logo {
    color: #000;
    img {
      height: 30px;
      margin: 17px 0 0 15px; } }
  .button-collapse {
    color: #000; }
  ul {
    li {
      a {
        color: #673ab7; } } } }

#home,#speakers,#team,#schedule {
  width: 100%;
  background: #fff;
  display: none;
  opacity: 0;
  transition: all 0.2s ease;
  &.show {
    opacity: 1; }
  &.visible {
    display: block; } }
#home {
  &.tbd {
    height: 200px; }
  .header {
    height: calc(100vh - 56px);
    background: #Fff;
    position: relative;
    overflow: hidden;
    box-shadow: 0 2px 2px rgba(0,0,0,0.25), 0 4px 4px rgba(0,0,0,0.15);
    .img-wrapper {
      position: absolute;
      top: 50%;
      left: 50%;
      height: calc(80vh - 56px);
      z-index: 10;
      width: 80%;
      transform: translate(-50%,-50%);
      img {
        height: 100%;
        width: 100%;
        object-fit: contain; } }
    .icon {
      position: absolute;
      z-index: 1;
      top: -100px;
      left: -100px; } }
  .stats {
    min-height: 250px;
    background: #673ab7;
    padding: 10px;
    .discription {
      color: #fff;
      font-weight: 300;
      font-size: 1.4em; }
    .stat-wrapper {
      padding-top: 10px;
      padding-bottom: 10px;
      color: #fff;
      .number {
        font-size: 3.5em;
        font-family: Roboto;
        font-weight: 300;
        line-height: 1.4; }
      .text {
        font-weight: 300; } } }
  .speakers {
    .title {
      font-size: 3em;
      font-weight: 300;
      padding: 20px; }
    .speaker-wrapper {
      margin-top: 20px;
      .img-wrapper {
        width: 130px;
        height: 130px;
        margin: 0 auto 15px auto;
        @media #{$small-and-down} {
          width: 110px;
          height: 110px; }
        border-radius: 500px;
        overflow: hidden;

        img {
          width: 100%;
          height: 100%; } }
      .name {
        font-size: 1.2em;
        font-weight: 400; }
      .line {
        color: #90a4ae;
        font-weight: 300; } } }
  .details {
    .organizer,.about-gdg {
      padding: 20px;
      .title {
        font-size: 3em;
        font-weight: 300;
        padding: 20px  0; }
      .text {
        font-weight: 200;
        font-size: 1.4em;
        padding-bottom: 20px; }
      .link {
        i {
          vertical-align: middle; } } } }

  .partners {
    .title {
      font-size: 3em;
      padding: 20px;
      font-weight: 300; }
    .sub-title {
      font-size: 1.4em;
      font-weight: 300; }
    .content {
      vertical-align: middle;
      .text {
        color: #90a4ae;
        padding: 10px 0; }
      .img-wrapper {
        width: 200px;
        margin: auto;
        padding: 10px;
        img {
          width: 100%; } } } } }
#schedule {
  .title-warpper {
    background: #fff;
    height: 300px;
    @media (max-width: 600px) {
      height: 150px; }
    width: 100%;
    position: relative;
    box-shadow: 0 2px 2px rgba(0,0,0,0.2),0 4px 4px rgba(0,0,0,0.1);
    .title {
      position: absolute;
      bottom: 100px;
      left: 50px;
      color: #673AB7;
      font-size: 3em;
      @media (max-width: 600px) {
        bottom: 30px;
        font-size: 2em; } } }
  .wrapper {
    .day-wrapper {
      line-height: 67px;
      height: 70px;
      font-size: 1.4em;
      border-bottom: 1px solid #cecece;
      padding: 0 10px;
      .text {
        padding: 0 10px;
        border-bottom: 2px solid #673AB7;
        display: inline-block; } }
    .schedule-wrapper {
      padding: 50px 0 50px 30px;
      .schedule-item {
        margin: 20px 0;
        .time-wrapper {
          vertical-align: top;
          .big {
            font-size: 3em;
            font-weight: 100;
            color: #7f7f7f; }
          .small {
            font-size: 1.2em;
            font-weight: 100;
            color: #7f7f7f; } }
        .details {
          margin-left: 20px;
          border: 1px solid #cecece;
          padding: 15px;
          width: 400px;
          @media (max-width: 600px) {
            width: 250px; }
          .title-wrapper {
            height: 100px;
            .title {
              font-size: 1.5em;
              font-weight: 400; }
            .difficulty {
              color: #888888; } }
          .session-details-wrapper {
            padding-bottom: 10px;
            .time {
              color: #888888; }
            .track {
              &.mobile {
                display: inline-block;
                padding: 0 5px;
                border: 1px solid #44ce4a;
                color: #44ce4a; } } }
          .speaker-wrapper {
            .img-wrapper {
              height: 50px;
              width: 50px;
              border-radius: 50px;
              overflow: hidden;
              img {
                height: 100%;
                width: 100%;
                object-fit: cover; } }
            .text {
              vertical-align: top;
              padding-left: 10px;
              .name,.about {
                line-height: 25px;
                vertical-align: middle; }
              .name {
                font-size: 1.2em; }
              .about {
                color: #888888; } } } } } } } }
#speakers,#team {
  .title-warpper {
    background: #fff;
    height: 300px;
    @media (max-width: 600px) {
      height: 150px; }
    width: 100%;
    position: relative;
    box-shadow: 0 2px 2px rgba(0,0,0,0.2),0 4px 4px rgba(0,0,0,0.1);
    .title {
      position: absolute;
      bottom: 150px;
      left: 50px;
      color: #673AB7;
      font-size: 3em;
      @media (max-width: 600px) {
        top: 10px;
        font-size: 2em; } }
    .text {
      position: absolute;
      bottom: 30px;
      left: 50px;
      font-size: 1.3em;
      @media (max-width: 600px) {
        bottom: 10px;
        font-size: 1.1em; }
      max-width: 700px; } }
  .speaker-wrapper {
    margin: 50px 0; }
  .speaker {
    max-width:  250px;
    margin: auto;
    text-align: center;
    box-shadow: 0 0 2px 0 rgba(0,0,0,0.07), 0 2px 2px 0 rgba(0,0,0,0.15);
    padding: 10px;
    transition: all 0.5s ease;
    &:hover {
      box-shadow: 0 0px 8px 0 rgba(0,0,0,0.07), 0 4px 4px 0 rgba(0,0,0,0.40); }
    .img-wrapper {
      width: 150px;
      height: 150px;
      border-radius: 100px;
      margin: auto;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover; } }
    .name {
      font-size: 1.6em; }
    .about {
      color: #888888;
      .place {
        padding: 10px 0; } } } }
.footer {
  background: #f3f3f3;
  padding: 10px;
  .logo {
    .img-wrapper {
      height: 45px;
      width: auto;
      @media (max-width: 750px) {
        width: 300px;
        height: auto;
        padding: 0 5px; }
      img {
        height: 100%;
        @media (max-width: 750px) {
          width: 100%; } } } }
  .copyright {
    @media (min-width: 750px) {
      padding-top: 20px;
      float: right; }
    font-weight: 300;
    color: #909090; } }
.tbd {
  height: 520px;
  font-size: 3.1em;
  text-align: center;
  padding: 20px;
  color: #673AB7;
  font-weight: 200;
  &.home {
    height: 200px; } }
